.mobile-card{
    display: inline-grid;
    grid-template-columns: max-content max-content;
    padding: 6px;
    margin: 10px;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    background-color: rgb(224, 224, 224);
    
}


img{
    border-radius: 10px;
}

